import React from "react";
import classes from './ProfileInfo.module.css';
import ProfileStatus from './ProfileStatus';
const ProfileInfo = (props) => {
    const onPhotoUpdate = (e) => {
        props.updatePhoto(e.target.files[0]);
    }
    return (<>
            <div className={classes.profile__info}>
                <img className={classes.profile__img}
                     src={props.profile.photos.large || "data:image/png;base64,"}
                     alt=""/><img/>

                <div className={classes.profile__descr}>
                    <div className={classes.profile__name}>{props.profile.fullName}</div>
                    <div>Date of birth: December 19</div>
                    <div>City: Rivne</div>
                    <div>Education: KPI</div>
                    <div>Web site: {props.profile.contacts.website}</div>
                    <div></div>

                </div>
            </div>
        {!props.anotherUserId && <input type="file" onChange={onPhotoUpdate}></input>}
    <ProfileStatus updateStatus={props.updateStatus} status={props.status}/>
    </>
    )
}
export default ProfileInfo;